import database from './database/database';

function createRecipeComponents(recipes) {}

function getRecipes() {
  const googleLoggedIn = parseInt(sessionStorage.getItem('googleLoggedIn'));
  const userLoggedIn = parseInt(sessionStorage.getItem('userLoggedIn'));
  let user;
  if (googleLoggedIn) {
    user = JSON.parse(sessionStorage.getItem('googleUser'));
  } else if (userLoggedIn) {
    user = JSON.parse(sessionStorage.getItem('loggedInUser'));
  }
  const favoritesRef = database.child('favorites');
  const recipesRef = favoritesRef.child(`${user.uid}`).child('recipes');
  recipesRef.once('value').then((recipes) => {
    const recipeData = recipes.val();
    createRecipeComponents(recipeData);
  });
}

function getRestaruants() {
  const googleLoggedIn = parseInt(sessionStorage.getItem('googleLoggedIn'));
  const userLoggedIn = parseInt(sessionStorage.getItem('userLoggedIn'));
  let user;
  if (googleLoggedIn) {
    user = JSON.parse(sessionStorage.getItem('googleUser'));
  } else if (userLoggedIn) {
    user = JSON.parse(sessionStorage.getItem('loggedInUser'));
  }
  const favoritesRef = database.child('favorites');
  const restaurantsRef = favoritesRef.child(`${user.uid}`).child('restaurants');
  restaurantsRef.once('value').then((restaurants) => {
    const restaurantData = restaurants.val();
    console.log(restaurantData);
  });
}

export function loadRecipes() {
  getRecipes();
}

export function loadRestaurants() {
  getRestaruants();
}
